<template>
  <v-app>
    <v-content>
      <v-container>
        <v-card>
          <v-card-text>
            <v-row>
              <v-col cols="12">
                <v-data-table
                  show-expand
                  :singgle-expand="true"
                  :items="forms"
                  :headers="[
                    {text:'Nama Database', value: 'name'},
                    {text:'Total Record', value: 'total'},
                    {text: 'Delete', value:'delete'},
                  ]"
                  item-key="id"
                >
                  <template v-slot:top>
                    <v-toolbar flat>
                      <v-toolbar-title>Pengaturan Kolom</v-toolbar-title>
                      <v-divider vertical class="mx-4"></v-divider>
                      <v-spacer></v-spacer>
                      <v-chip dark color="green" @click="addForms">Tambah Kolom</v-chip>
                      <v-divider vertical class="mx-2"></v-divider>
                      <v-chip dark color="green" @click="save">Save</v-chip>
                    </v-toolbar>
                  </template>
                  <template v-slot:item.name="{item}">
                    <v-text-field v-model="item.name" label="Nama"></v-text-field>
                  </template>
                  <template v-slot:item.total="{item}">{{item.fields.length}}</template>
                  <template v-slot:item.delete="{item}">
                    <v-icon @click="deleteForms(item)">mdi-delete-outline</v-icon>
                  </template>
                  <template v-slot:expanded-item="{ headers, item }">
                    <td :colspan="headers.length">
                      <v-data-table
                        :hide-default-footer="true"
                        :items="item.child"
                        :headers="header"
                        class="my-4"
                      >
                        <template v-slot:top>
                          <v-toolbar flat>
                            <v-toolbar-title>List Record</v-toolbar-title>
                            <v-spacer></v-spacer>
                            <v-chip @click="addField(item)">Tambah Record</v-chip>
                          </v-toolbar>
                        </template>
                        <template v-slot:item.name="{item}">
                          <v-text-field v-model="item.name" label="Nama"></v-text-field>
                        </template>
                        <template v-slot:item.additional="{item}">
                          <v-text-field v-model="item.additional" label="Ukuran"></v-text-field>
                        </template>
                        <template v-slot:item.type="{item}">
                          <v-autocomplete
                            v-model="item.type"
                            :items="typeData"
                            class="mx-4"
                            flat
                            label="Type data"
                          ></v-autocomplete>
                        </template>
                        <template v-slot:item.nullable="{item}">
                          <v-checkbox v-model="item.nullable"></v-checkbox>
                        </template>
                        <template v-slot:item.image="{item}">
                          <v-checkbox v-model="item.image"></v-checkbox>
                        </template>
                      </v-data-table>
                    </td>
                  </template>
                </v-data-table>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
export default {
  data() {
    return {
      id: 1,
      header: [
        // { text: "ID", value: "id" },
        { text: "Nama kolom", value: "name" },
        { text: "Type data", value: "type" },
        { text: "Ukuran", value: "additional" },
        { text: "Nullable", value: "nullable" },
        { text: "Image", value: "image" },
        { text: "Action", value: "action" }
      ],
      forms: [
        {
          id: 1,
          idF: 1,
          name: null,
          fields: [
            {
              id: 1,
              name: null,
              type: null,
              nullable: false,
              image: false
            }
          ]
        }
      ],
      defaultForm: {
        id: 1,
        name: null,
        fields: [
          {
            id: 1,
            name: null,
            type: null,
            nullable: false,
            image: false
          }
        ]
      },
      defaultField: {
        id: 1,
        name: null,
        type: null,
        nullable: false,
        image: false
      },
      typeData: [
        "id",
        "foreignId",
        "bigIncrements",
        "bigInteger",
        "binary",
        "boolean",
        "char",
        "date",
        "dateTime",
        "dateTimeTz",
        "decimal",
        "double",
        "enum",
        "float",
        "string", //req other params
        "geometry",
        "geometryCollection",
        "increments",
        "integer",
        "ipAddress",
        "json",
        "json",
        "lineString",
        "longText",
        "macAddress",
        "mediumIncrements",
        "mediumInteger",
        "mediumText",
        "morphs",
        "uuidMorphs",
        "multiLineString",
        "multiPoint",
        "softDeletes",
        "text",
        "time",
        "timestamp",
        "tinyInteger",
        "unsignedInteger",
        "unsignedMediumInteger",
        "uuid",
        "year"
      ]
    };
  },
  mounted() {
    this.forms = this.$store.state.databases;
  },
  methods: {
    addForms() {
      let inc = (this.id += 1);
      this.defaultForm.id = inc + "forms";
      let form = Object.assign({}, this.defaultForm);
      this.forms.push(form);
      this.id = inc;
    },
    addField(item) {
      let inc = (this.idF += 1);
      this.defaultField.id += 1 + "fields";
      let index = this.forms.indexOf(item);
      let form = Object.assign({}, this.defaultForm);
      this.forms[index].fields.push(form);
      this.idF = inc;
      this.save();
    },
    deleteForms(item) {
      let index = this.forms.indexOf(item);
      confirm("Are you sure you want to delete this DB?") &&
        this.forms.splice(index, 1);
    },
    save() {
      this.$store.commit("databases", this.forms);
    }
    // deleteField(item){
    //   let idf = this.forms.indexOf(item);
    //   let idr = this.forms[idf].fields.indexOf()
    //   confirm('Are you sure you want to delete this Record?') && this.forms.splice(index, 1)
    // },
  }
};
</script>
